<template>
  <div id="container">
    <header class="header">
      <transition name="donghua" appear mode="out-in">
        <router-view class="z_"></router-view>
      </transition>
    </header>
    <footer class="footer">
      <ul>
        <router-link v-for="value in list" :to="value.path" custom v-slot="{navigate,isActive}" :key="value.id">
          <li @click="navigate" :class="isActive?'active':''"><i :class="value.icon"></i>{{ value.name }}</li>
        </router-link>
      </ul>
    </footer>
  </div>
</template>
<script type="text/javascript">
  import axios from "axios"
  import {wgs84togcj02} from "@/utils/distance.js"
  import Vue from "vue"
  import {
    Toast,
    Dialog
  } from 'vant';

  // 全局注册
  Vue.use(Dialog);
  Vue.use(Toast);
  export default {
    data() {
      return {
        list: [{
          id: 0,
          icon: "iconfont icon-dianying",
          name: "电影",
          path: "/main/movie"
        },
          {
            id: 1,
            icon: "iconfont icon-yingyuan",
            name: "影院",
            path: "/main/cinema"
          },
          {
            id: 2,
            icon: "iconfont icon-wode",
            name: "我的",
            path: "/main/mine"
          }
        ],
        isOn: false,
      }
    },
    mounted() {
      window.onload = () => {
        if (this.$store.state.isPosition) {
          this.city()
          this.$store.commit("position", false)
        }
      }
    },
    methods: {
      error(a = null) {
        Dialog.confirm({
          title: '定位失败',
          message: a === null ? '无法获取当前城市定位，请手动选择所在城市' : a,
        })
          .then(() => {
            this.$router.push('/main/city')
          })
          .catch(() => {

          });
      },
      city() {
        let this_ = this
        Toast.loading({
          message: '定位中...',
          forbidClick: true,
          duration: 0
        });
        try {
          let geolocation = new BMap.Geolocation();
          geolocation.enableSDKLocation();
          geolocation.getCurrentPosition(function getinfo(position) {
            let city = position.address.city;

            axios({
              url: "https://m.maizuo.com/gateway?k=7102382",
              headers: {
                'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16352477313746499972300801","bc":"110100"}',
                'X-Host': 'mall.film-ticket.city.list'
              }
            })
              .then(value => {
                value.data.data.cities.forEach(v => {
                  if (v.name === city.slice(0, city.length - 1)) {
                    this_.$store.commit("changeCity", v)
                  }
                })
              })
            navigator.geolocation.getCurrentPosition((location) => {
              let obj = {
                longitude: wgs84togcj02(location.coords.longitude, location.coords.latitude)[0],
                latitude: wgs84togcj02(location.coords.longitude, location.coords.latitude)[1],
              }

              this_.$store.commit("setXy", obj)
              this_.isOn = true
            }, () => {
            }, {timeout: 30000, enableHighAccuracy: true, maximumAge: 75000})


            setTimeout(() => {
              if (this_.isOn) {
                Toast.clear()
              } else {
                Toast.clear()
                this_.error("位置信息获取失败")
              }
            }, 1000)
          }, function (e) {
            Toast.clear()
            this_.error()
          }, {
            provider: 'baidu'
          });
        } catch (e) {
          Toast.clear()
          this_.error()
        }
      },

    }
  }
</script>

<style type="text/css" lang="scss" scoped>

  .z_ {
    position: absolute;
  }

  .footer {
    width: 100%;
    height: 3.125rem;
    position: fixed;
    bottom: -1px;
    border-top: 0.0625rem solid #ededed;

    ul {
      display: flex;
      height: 3.125rem;
      align-items: center;
      background-color: #ffffff;
      justify-content: space-around;

      li {
        list-style: none;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        i {
          padding-top: 3px;
          font-size: 20px;
          margin-right: 5px;
        }
      }
    }
  }

  .active {
    background-color: #ffffff;
    color: #ff972e;
  }

  @keyframes o1 {
    0% {
      opacity: 0;
      transform: translateY(20px)
    }

    100% {
      opacity: 1;
      transform: translateY(0)
    }
  }

  .donghua-enter-active {
    animation: o1 400ms ease-in-out;
  }

  .donghua-leave-active {
    animation: o1 400ms ease-in-out reverse;
  }
</style>
